import { Component } from "react";
import Button from "./components/Button";
import "./css/styles.css";

class App extends Component {
  constructor() {
    super();

    this.state = {
      current: "0",
      previous: [],
      nextIsReset: false,
    };
  }

  reset = () => {
    this.setState({ current: "0", previous: [], nextIsReset: false });
  };

  addToCurrent = (currentSymbol) => {
    const symbolsNeedingAnotherAction = ["/", "*", "-", "+"];

    if (symbolsNeedingAnotherAction.includes(currentSymbol)) {
      let previous = [...this.state.previous];
      const firstElementInPrevious = previous[0];

      if (firstElementInPrevious === this.state.current) {
        previous[previous.length - 1] = ` ${currentSymbol} `;
      } else if (previous.length > 1) {
        const result = this.calculate();
        previous = [result, ` ${currentSymbol} `];
      } else {
        previous.push(this.state.current, ` ${currentSymbol} `);
      }
      this.setState({ previous, nextIsReset: true });
    } else {
      if (
        (this.state.current === "0" && currentSymbol !== ".") ||
        this.state.nextIsReset
      ) {
        this.setState({ current: currentSymbol, nextIsReset: false });
      } else {
        this.setState({ current: this.state.current + currentSymbol });
      }
    }
  };

  calculate = () => {
    const { current, previous } = this.state;
    const result = eval(`${previous.join("")}${current}`);
    this.setState({ current: result, previous: [], nextIsReset: true });
    return result;
  };

  render() {
    const buttons = [
      { symbol: "C", cols: 3, action: this.reset },
      { symbol: "/", cols: 1, action: this.addToCurrent },
      { symbol: "7", cols: 1, action: this.addToCurrent },
      { symbol: "8", cols: 1, action: this.addToCurrent },
      { symbol: "9", cols: 1, action: this.addToCurrent },
      { symbol: "*", cols: 1, action: this.addToCurrent },
      { symbol: "4", cols: 1, action: this.addToCurrent },
      { symbol: "5", cols: 1, action: this.addToCurrent },
      { symbol: "6", cols: 1, action: this.addToCurrent },
      { symbol: "-", cols: 1, action: this.addToCurrent },
      { symbol: "1", cols: 1, action: this.addToCurrent },
      { symbol: "2", cols: 1, action: this.addToCurrent },
      { symbol: "3", cols: 1, action: this.addToCurrent },
      { symbol: "+", cols: 1, action: this.addToCurrent },
      { symbol: "0", cols: 2, action: this.addToCurrent },
      { symbol: ".", cols: 1, action: this.addToCurrent },
      { symbol: "=", cols: 1, action: this.calculate },
    ];

    return (
      <div className="App">
        <div className="result">
          <input
            className="result--main"
            type="text"
            name=""
            id=""
            value={this.state.current}
          />

          {this.state.previous.length > 0 ? (
            <small className="result--small">
              {this.state.previous}
              {/* {this.state.previous[this.state.previous.length - 1]} */}
            </small>
          ) : null}
        </div>

        <div className="">
          {buttons.map((btn, index) => {
            return (
              <Button
                key={index}
                symbol={btn.symbol}
                cols={btn.cols}
                action={(symbol) => btn.action(symbol)}
              />
            );
          })}
        </div>
      </div>
    );
  }
}
export default App;